#pSystemLoginPhone {
	background: transparent;
	position: relative;
	.input.playKeyInput:nth-child(1) {
		float: left;
		margin-left: unit(@systemInputMargin, px);
		width: unit(@systemInputWidth, px);
	}
	.input.playKeyInput:nth-child(2) {
		float: right;
		margin-right: unit(@systemInputMargin, px);
		width: unit(@systemInputWidth, px);
	}

	.grid.keyboardFull {
		width: unit(@keyboardKeySize * 5, px);
		margin: 0 auto;
		margin-top: unit(@pMainMenuP * 2.6, px);

	}
	.button.blueBtn.code {
		float: left;
		position: absolute;
		top: unit(@pMainMenuP * 3, px);
		left:unit(@systemButtonMargin, px);
	}
	.button.blueBtn.login {
		float: left;
		position: absolute;
		top: unit(@pMainMenuP * 3, px);
		right:unit(@systemButtonMargin, px);
	}
}
