#pSearch {

}


#pSearchHeader {
	width      : unit(@width - @pMainMenuP, px);
	height     : unit(@headerH, px);
	background : url('../img/@{height}/logo.png') right no-repeat;
	padding    : unit(@pMainMenuP, px);
}

#pSearchHeaderBackBtn {
	background  : transparent;
	height      : auto;
	padding     : 0;
	font-size   : unit(@fontSize, px);
	color       : #909090;
	line-height : 1;
	.text {
		padding : 0;
		&::before {
			content       : '< ';
			padding-right : 4px;
		}
	}
	&.focus {
		color : #0D57B1;
	}
}


#pSearchList {
	background : transparent;
	width      : auto;
	position   : relative;
	left       : unit(-@searchListL, px);
	.item {
		width      : unit(@searchElemW, px);
		padding: 0;
		text-align : left;
		//height : unit(@searchFocusElemH, px);
		box-sizing: border-box;
		padding-right: 10px;
		img {
			width  : unit(@searchElemW, px);
			height : unit(@searchElemH, px);
			margin: 0;
			box-sizing: border-box;
		}
		&.empty {
			visibility: hidden;
		}
	}
	.item.focus {
		width      : unit(@searchFocusElemW, px);
		background : transparent;
		height : unit(@searchFocusElemH, px);
		padding-right: 5px;
		img {
			width  : unit(@searchFocusElemW, px);
			height : unit(@searchFocusElemH, px);
			border : 1px solid transparent;
		}

	}
	&.focus .item.focus img {
		border : 1px solid #0D57B1;
	}
}

#pSearchInput {
	margin: 0 auto;
	font-size: unit(@fontSize * 2.0, px);
	line-height: 1;
	text-transform: uppercase;
	width: 40%;
}

#pSearchKeyboard {
	margin-top: unit(@fontSize * 1.2 , px);
}

#pSearchMessage {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	text-align: center;
	font-size: unit(@fontSize * 2, px);
	width: unit(@width, px);
	height: unit(@fontSize * 4, px);
}
