#pageCatalog {
	&::after {
		content : '';
		display: block;
		width: 130px;
		height: 100%;
		position: absolute;
		right: 0;
		top: 0;
		background: url('../img/@{height}/fade.png');

	}
}

#pCatalogHeader {
	width      : unit(@width - @pMainMenuP, px);
	height     : unit(@headerH, px);
	background : url('../img/@{height}/logo.png') right no-repeat;
	padding    : unit(@pMainMenuP, px);
	z-index: 1;
}

#pCatalogHeaderBackBtn {
	background  : transparent;
	height      : auto;
	padding     : 0;
	font-size : unit(@headerFontS * 0.85, px);
	color       : #909090;
	line-height : 1;
	.text {
		padding : 0;
		&::before {
			content       : '< ';
			padding-right : 4px;
		}
	}
	&.focus {
		color : #0D57B1;
	}
}

#pCatalogTitle {
	display   : inline-block;
	font-size : unit(@headerFontS, px);
	padding-left: unit(@pMainMenuP * 0.5, px);
}
#pCatalogNumber {
	display   : inline-block;
	font-size : unit(@headerFontS * 0.85, px);
	padding-left: unit(@pMainMenuP * 0.5, px);
	color       : #909090;
}
#pCatalogList {
	background : transparent;
	width      : auto;
	position   : relative;
	left       : unit(-@pCatalogImgW * 0.8, px);
	.item {
		width      : unit(@pCatalogImgW + 15, px);
		text-align : left;
		img {
			width  : unit(@pCatalogImgW, px);
			height : unit(@pCatalogImgH, px);
			border : 2px;
		}
		&.empty {
			visibility: hidden;
		}
	}
	.item.focus {
		width      : unit(@pCatalogFImgW + 10, px);
		background : transparent;
		img {
			width  : unit(@pCatalogFImgW, px);
			height : unit(@pCatalogFImgH, px);
			border : 1px solid transparent;
		}

	}
	&.focus .item.focus img {
		border : 1px solid #0D57B1;
	}
}

#pCatalogTable {
	width: unit(@width - @pMainMenuP * 3, px);
	table-layout: fixed;
	margin-left: unit(@pCatalogTableM, px);
	.left {
		width: unit(@pCatalogFImgW, px);
		vertical-align: top;
	}
	.right {
		vertical-align: top;
	}
}

#pcatalogGameTitle {
	padding-left: unit(@pMainMenuP, px);
	font-size: unit(@fontSize * 1.5, px);
}

#pcatalogGameDescription {
	padding-left: unit(@pMainMenuP, px);
	font-size: unit(@fontSize * 0.7, px);
}

#pCatalogPlayBtn {
	width: 100%;
	background: transparent;
	border: 1px solid #F78C00;
	&.focus {
		background: #F78C00;
	}
	span {
		display: none;
	}
	&.notAuth {
		background: url('../img/@{height}/btn.play.account.png');
		background-size:100% 100%;
		border: none;
		.text {
			font-size: unit(@fontSize * 0.75, px);
			padding: 0;
			text-align: left;
			padding-left: 10px;
			color: #999999;
		}
		span {
			padding-right: 10px;
			font-size: unit(@fontSize * 0.85, px);
			display: table-cell;
			vertical-align: middle;
			text-align: right;
		}
		&.focus {
			background: #F78C00;
			.text {
				display: none;
			}
			span {
				text-align: center;
				padding: 0;
				font-size: unit(@fontSize, px);
			}
		}
	}
}

#pCatalogInfo {
 span {
	 display: block;
	 float: left;
	 color: #909090;
	 font-size: unit(@fontSize * 0.9, px);
	 &.infoSpan {
		 color: #ffffff;
		 font-size: unit(@fontSize, px);
		 float: none;
		 &::before {
			 content: '\00a0';
		 }
	 }
 }
}
