#modalPopUp {
	width: unit(@width, px);
	height: unit(@height, px);
	background: rgba(0,0,0, 0.7);
	z-index: 4;
	.panel{
		background: #021945;
		width: unit(@popupWidth, px);
		height: unit(@popupHeight, px);
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		text-align: center;
	}
	.orangeBtn {
		background: transparent;
		border: 1px solid #F78C00;
		margin: 2%;
		width: unit(@keyboardKeySize * 5.5, px);
		&.focus {
			background: #F78C00;
		}
	}
	.blueBtn {
		margin: 2%;
	}
}
