#pageMain {
	background : url('../img/@{height}/bg.jpg');
	&::after {
		display: block;
		content: '';
		width: 100%;
		height: unit(@pMainShadowH, px);
		background: url('../img/@{height}/shadow.png');
		position: fixed;
		bottom: 0;
		left: 0;
	}
	&::before {
		display: block;
		content: '';
		width: 100%;
		height: unit(@pMainShadowH, px);
		background: url('../img/@{height}/shadow.png');
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;
		-webkit-transform: rotate(180deg);
	}
}

#pMainHeader {
	width      : unit(@width - @pMainMenuP, px);
	height     : unit(@headerH, px);
	background : url('../img/@{height}/logo.png') right no-repeat;
	position: relative;
	z-index: 2;
}

#pMainMenu {
	background : transparent;
	//-webkit-transition: top 1s ease-out 10ms;
	width      : unit(@pMainMenuW, px);
	position: absolute;
	left: 0;
	.item {
		font-size: unit(@fontSize * 0.9, px);
		padding-left: unit(@pMainMenuP, px);
		&.focus {
			background : transparent;
			color      : #0D57B1;
			font-size: unit(@fontSize*0.95, px);
		}
	}

}


#pMainRight {
	position : fixed;
	top: unit(@pMainPreviewT, px);
	left: unit(@pMainMenuW, px);
	width: 100%;
	img {
		margin-right: unit(@pMainPreviewImgM, px);
		width: unit(@pMainPreviewImgW, px);
		height: unit(@pMainPreviewImgH, px);
		border-radius: 2px;
	}


}
#pMainLoader {
	display: none;
	background: url('../img/@{height}/loader.3.GIF') center no-repeat;
	background-size: 100%;
	width: 32px;
	height: 32px;
	position: absolute;
	bottom: 80px;
	right: 80px;
}

.modalBox.playkeyModal {
	background: rgba(0,0,0,0.7);
	.body {
		background: #021945!important;
		width: unit(@popupWidth , px);
		text-align: center;
		padding-left: 0!important;
		padding-right: 0!important;
	}
	.orangeBtn {
		background: transparent;
		border: 1px solid #F78C00;
		margin: 2%;
		width: unit(@keyboardKeySize * 5.5, px);
		&.focus {
			background: #F78C00;
		}
	}
	.blueBtn {
		margin: 2%;
	}
}
