.clr {
	clear: both;
}

#pG {
	.left {
		width: unit(@pGLeftW, %);
		float: left;
		height: unit(@height, px);
	}

	.right {
		float: right;
		width: unit(@pGRightW, %);
	}

}

#pGgHeader {
	width      : unit(@width - @pMainMenuP, px);
	height     : unit(@headerH, px);
	background : url('../img/@{height}/logo.png') right no-repeat;
	padding    : unit(@pMainMenuP, px);
	z-index    : 1;
}

#pGHeaderBackBtn {
	background  : transparent;
	height      : auto;
	padding     : 0;
	font-size : unit(@headerFontS * 0.85, px);
	color       : #909090;
	line-height : 1;
	.text {
		padding : 0;
		&::before {
			content       : '< ';
			padding-right : 4px;
		}
	}
	&.focus {
		color : #0D57B1;
	}
}

#pGTitle {
	display      : inline-block;
	font-size : unit(@headerFontS, px);
	padding-left : unit(@pMainMenuP * 0.5, px);
}

#pGFullCard {
	padding: 0;
	background-color: transparent;
}
#pGFullDescription {
	background-color: transparent;
	padding: 0 15%;
}
#pGPoster {
	width             : unit(@pCatalogFImgW + 2, px);
	background        : transparent;
	height            : unit(@pCatalogFImgH + 2, px);
	border            : 1px solid transparent;
	background-size   : 100% 100%;
	background-repeat : no-repeat;
	margin-left: unit(@pGCMarginLeft, px);
	&.focus {
		border : 1px solid #0D57B1;
	}
}



#pGScreenshots {
	//display: none;
	width: 20%;
	height: unit(@pGScreenH *1.2, px);
	background: transparent;
	//background: red;
	.item {
		width: unit(@pGScreenW + 10, px);
		padding: 0;
		position: relative;
		.wrapper {
			position: relative;
		}
		img {
			width: unit(@pGScreenW, px);
			height: unit(@pGScreenH, px);
			border : 1px solid transparent;

		}
		&.focus {
			background: transparent;
		}
		&.video .wrapper::before {
			content: '';
			display: block;
			position: absolute;
			width: unit(@pGPlayIcoSize, px);
			height: unit(@pGPlayIcoSize, px);
			top:0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			background: url('../img/@{height}/play.ico.png') center no-repeat;

		}
	}
	&.focus .item.focus img {
		border : 1px solid #0D57B1;
	}
}
#pGLeftInfo {
	width: 48%;
	float: left;
	div:nth-child(odd) {
		float: left;
		color: #909090;
		line-height: 1;
		font-size: unit(@fontSize * 0.9, px);
	}
	div:nth-child(even) {
		font-size: unit(@fontSize * 0.95, px);
		line-height: 1;
		color: #ffffff;
		max-height: unit(@fontSize * 0.95, px);
		overflow: hidden;
		&::before {
			content: '\00a0';
		}
	}
}
#pGRightInfo {
	width: 48%;
	float: left;
	div:nth-child(odd) {
		float: left;
		color: #909090;
		font-size: unit(@fontSize * 0.9, px);
	}
	div:nth-child(even) {
		font-size: unit(@fontSize * 0.95, px);
		color: #ffffff;
		&::before {
			content: '\00a0';
		}
	}

}

#pGDevices {
	width: 100%;
	height: unit(@fontSize * 0.85, px);
	margin: unit(@fontSize * 0.70, px) 0;
	display: table;
	div {
		font-size: unit(@fontSize * 0.85, px);
		//width: 20%;
		margin-right: unit(@fontSize, px);
		display: inline-block;
	}
}

#pGDescription {
	font-size: unit(@fontSize * 0.85, px);
	line-height: 1;
	max-height: unit(@fontSize * 5.0, px);
	display: inline;
}
#pGReadMore {
	display: inline-block;
	background: transparent;
	height: auto;
	padding: 0;
	color: #909090;
	//float: right;
	&.focus {
		color: #0D57B1;
	}
	.text {
		padding: 0;
		line-height: 0.8;
		font-size: unit(@fontSize * 0.85, px);
	}
}

// icons
#pGKeyboard::before{
	height: unit(@deviceIcoSize, px);
	width:unit(@deviceIcoSize, px);
	content: '';
	display: inline-block;
	background: url('../img/@{height}/icons.sprite.png') top left;
	float: left;
	margin-right: 2px;
	font-size: unit(@fontSize * 0.85, px);
	line-height: unit(@fontSize * 0.85, px);
	margin-top: 2px;
}
#pGMouse::before{
	content: '';
	display: inline-block;
	height: unit(@deviceIcoSize, px);
	width:unit(@deviceIcoSize, px);
	background: url('../img/@{height}/icons.sprite.png') top right;
	float: left;
	margin-right: 2px;
	margin-top: 2px;
}
#pGGamepad::before{
	content: '';
	display: inline-block;
	height: unit(@deviceIcoSize, px);
	width:unit(@deviceIcoSize, px);
	background: url('../img/@{height}/icons.sprite.png') bottom left;
	float: left;
	margin-right: 2px;
	margin-top: 2px;
}
#pGMultiplayer::before{
	content: '';
	display: inline-block;
	height: unit(@deviceIcoSize, px);
	width:unit(@deviceIcoSize, px);
	background: url('../img/@{height}/icons.sprite.png') bottom right;
	float: left;
	margin-right: 2px;
	margin-top: 2px;
}

#pGSystemWrapper {
	.authPlayBtn {
		background: url('../img/@{height}/btn.play.account.png');
		background-size:100% 100%;
		width             : unit(@pCatalogFImgW, px);
		margin-left: unit(@pGCMarginLeft, px);
		border: none;
		margin-top: unit(@fontSize * 0.88, px);
		.text {
			font-size: unit(@fontSize * 0.75, px);
			padding: 0;
			text-align: left;
			padding-left: 10px;
			color: #999999;
		}
		span {
			padding-right: 10px;
			font-size: unit(@fontSize * 0.85, px);
			display: table-cell;
			vertical-align: middle;
			text-align: right;
		}
		&.focus {
			background: #F78C00;
			.text {
				display: none;
			}
			span {
				text-align: center;
				padding: 0;
				font-size: unit(@fontSize, px);
			}
		}
	}

	.playFreeBtn {
		width             : unit(@pCatalogFImgW, px);
		margin-left: unit(@pGCMarginLeft, px);
		background: transparent;
		border: 1px solid #F78C00;
		font-size: unit(@fontSize * 0.85, px);
		margin-top: unit(@fontSize * 0.88, px);
		&.focus {
			background: #F78C00;
		}

	}
	.subscribeBtn {
		width             : unit(@pCatalogFImgW, px);
		margin-left: unit(@pGCMarginLeft, px);
		background: transparent;
		border: 1px solid #0D57B1;
		font-size: unit(@fontSize * 0.85, px);
		margin-top: unit(@fontSize * 0.5, px);
		&.focus {
			background: #0D57B1;
		}
	}
	.playBtn {
		width             : unit(@pCatalogFImgW, px);
		margin-left: unit(@pGCMarginLeft, px);
		background: transparent;
		border: 1px solid #F78C00;
		font-size: unit(@fontSize * 0.85, px);
		margin-top: unit(@fontSize  * 0.88, px);
		&.focus {
			background: #F78C00;
		}
	}

}
