#pScreenshotPreview {
	background : transparent;
}

#pScreenshotPlayer.player {
	width:unit(@width, px);
	height: unit(@height, px);
	&.video {
		background: url('../img/@{height}/play.ico.png') center no-repeat;
	}
}

#pScreenshotPreviewLeft {
	position: absolute;
	bottom: unit(@pMainMenuP, px);
	left: unit(@pMainMenuP, px);
	font-size: unit(@fontSize * 1.7, px);
	color: #999999;
	&::after {
		content: "\f04a";
		font-family: 'icomoon';
	}
}
#pScreenshotPreviewRight {
	position: absolute;
	bottom: unit(@pMainMenuP, px);
	right: unit(@pMainMenuP, px);
	font-size: unit(@fontSize * 1.7, px);
	color: #999999;
	&::after {
		content: "\f04e";
		font-family: 'icomoon';
	}
}
#pScreenshotPreviewControl {
	display: table-cell;
	vertical-align: middle;
	position: absolute;
	bottom: 30px;
	left: unit(@controlIcoL, px);
	width: unit(@controlIcoW, px);
	height: unit(@controlIcoH, px);
	background: url('../img/@{height}/sticker.png');
	text-align: right;
	font-size: unit(@fontSize * 0.7, px);
	div {
		display: inline-block;
		position: absolute;
		bottom: unit(@controlIcoTextB, px);
		right: 0;
	}
}

#pScreenshotPreviewProgress {
	position: absolute;
	bottom: 10px;
	left: 0;
	width: unit(@width, px);
	height: 7px;
	background: transparent;
	.value {
		background: #127EFF;
	}
}

#pScreenshotPreviewImg {
	width: unit(@width, px);
	height: unit(@height, px);
	position: absolute;
	top: 0;
	left: 0;
	z-index: -10;
}
