#pSystemHeader {
	width      : unit(@width - @pMainMenuP, px);
	height     : unit(@headerH, px);
	background : url('../img/@{height}/logo.png') right no-repeat;
	padding    : unit(@pMainMenuP, px);

}

#pSystemBackBtn {
	background  : transparent;
	height      : auto;
	padding     : 0;
	font-size : unit(@headerFontS * 0.85, px);
	color       : #909090;
	line-height : 1;
	.text {
		padding : 0;
		&::before {
			content       : '< ';
			padding-right : 4px;
		}
	}
	&.focus {
		color : #0D57B1;
	}
}

#pSystemTitle {
	display   : inline-block;
	font-size : unit(@headerFontS, px);
}

#pageSystem {
	.systemList {
		background : transparent;
		width      : auto;
		margin     : 0 auto;
		.item {
			height : unit(@height - @headerH, px);
			width  : unit(@pSystemLoginFElemW + 15, px);
			.wrapper {
				position : relative;

			}
			.elem {
				width      : unit(@pSystemLoginElemW, px);
				height     : unit(@pSystemLoginElemH, px);
				background : #147DFB;
				position   : absolute;
				top        : 0;
				left       : 0;
				right      : 0;
				bottom     : 0;
				margin     : auto;
				.title {
					font-size   : unit(@menuTitleSize, px);
					width       : 100%;
					text-align  : left;
					line-height : 1.2;
					padding     : 7% 7% 0 7%;

				}
				.description {
					height      : 55%;
					font-size   : unit(@fontSize, px);
					width       : 100%;
					text-align  : left;
					line-height : 1.2;
					position    : absolute;
					bottom      : 0;
					left        : 0;
					padding     : 0 7%;

				}
			}
			.elemFocus {
				width      : unit(@pSystemLoginFElemW, px);
				height     : unit(@pSystemLoginFElemH, px);
				background : #147DFB;
				position   : absolute;
				top        : 0;
				left       : 0;
				right      : 0;
				bottom     : 0;
				margin     : auto;
				visibility : hidden;
				color      : #ffffff;
				.title {
					font-size   : unit(@menuTitleSize, px);
					width       : 100%;
					text-align  : left;
					line-height : 1.2;
					padding     : 7% 7% 0 7%;

				}
				.description {
					height      : 55%;
					font-size   : unit(@fontSize, px);
					width       : 100%;
					text-align  : left;
					line-height : 1.2;
					position    : absolute;
					bottom      : 0;
					left        : 0;
					padding     : 0 7%;
				}
			}

			&.focus {
				background : transparent;
				.elem {
					visibility : hidden;
				}
				.elemFocus {
					visibility : visible;
				}
			}
		}
	}
}

//keyboard
.grid.keyboardFull {
	background-color : transparent;
	table {
		table-layout : fixed;
		width        : auto;
		margin       : auto;
	}
	.item {
		padding : 0!important;
		//width      : unit(@keyboardKeySize+2, px);
		//height      : unit(@keyboardKeySize+2, px);
		&.focus {
			background: transparent!important;
		}
		.button {
			width          : unit(@keyboardKeySize, px);
			height         : unit(@keyboardKeySize, px);
			padding        : 0;
			text-transform : uppercase;
			background     : #ffffff;
			border-radius  : 3px;
			margin: 2px;
			box-sizing     : border-box;
			color          : #000000;
			font-size      : unit(@fontSize, px);
			line-height    : unit(@keyboardKeySize, px);
			border: 1px solid #ffffff;
			&.space {
				width          : unit(@keyboardKeySize * 2 + 4, px);
				background: transparent;
				border: 1px solid #ffffff;
				position: relative;
				&::after {
					content: '';
					display: block;
					width: unit(@keyboardKeySize * 1.2, px);
					height: unit(@keyboardKeySize * 0.2, px);
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					border: 2px solid #ffffff;
					border-top: none ;
				}

			}
			&.backSpace {
				background: transparent;
				border: 1px solid #ffffff;
				color:#ffffff;
				&::before {
					font-family: 'icomoon';
					content: '\ea40';
				}
			}
			&.changeLang {
				background: transparent;
				border: 1px solid #ffffff;
				text-transform: none;
				color: #ffffff;
				font-size: unit(@fontSize * 0.65, px);

			}
			&.plus {
				background: transparent;
				border: 1px solid #ffffff;
				color:#ffffff;
			}
		}

	}
	&.focus .item.focus {
		background-color : transparent!important;
		.button {
			background-color: #127EFF;
			color: #ffffff;
			border: 1px solid #127EFF;
		}
	}
}

// special inputs

.input.playKeyInput {
	background-color: transparent;
	border: none;
	border-bottom: 2px solid #909090;
	color: #ffffff;
	text-align: center;
	font-size: unit(@inputFontSize, px);
	&.focus {
		border-bottom: 2px solid #127EFF;
	}
	.caret{
		color: #ffffff;
	}
}


// special buttons

.button.blueBtn {
	background: transparent;
	border: 1px solid #127EFF;
	&.focus {
		background: #127EFF;
	}
	width: unit(@keyboardKeySize * 5.5, px);
}




