#pSystemProfile {
	height     : unit(@height - @headerH, px);
	background : transparent;
	.systemList {
		//.elem {
		.elemTitle {
			font-size   : unit(@menuTitleSize, px);
			line-height : 1;
			padding: unit(@fontSize, px);
			text-align: left;

		}
		.elemDescription {
			font-size : unit(@fontSize * 0.8, px);
			line-height: 1;
			text-align: left;
			padding: 0 unit(@fontSize, px);
			padding-top: unit(@fontSize * 3, px);

		}
		.elemText {
			font-size : unit(@fontSize, px);
			line-height: 1;
			text-align: left;
			padding: 0 unit(@fontSize, px);
		}

		//}
		.elemFocus {
		z-index: 8;
		}
	}
}
