#pSystemSubscribeSettings {
	height     : unit(@height - @headerH, px);
	background : transparent;
	text-align : center;
	.title {
		width      : 100%;
		text-align : center;
	}
	.date {
		width       : unit(@sAccountW, px);
		height      : unit(@sAccountH, px);
		margin      : 0 auto;
		margin-top : unit(@fontSize, px);
		font-size   : unit(@fontSize * 1.5, px);
		line-height : unit(@sAccountH, px);
		border: 2px solid #999999;
		border-radius: 6px;
	}
	.button {
		margin-top : unit(@fontSize * 3, px);
	}
}
